import review from '../media/task_review.png';
import read from '../media/task_read.png';
import interview from '../media/task_interview.png';
import teach from '../media/task_teach.png';

const taskList = [
    {name: "Sleeping", desc: "Even the best researchers would need some sleep at night!", img: sleep}, 
    {name: "Reviewing research proposals", desc: "(SAMPLE TEXT) Lorem ipsum dolor sit amet, consectetur adipiscing elit. In eget quam sed nisl tincidunt blandit. Sed ut lacinia erat, eget commodo lorem. Sed interdum massa vitae libero mollis dignissim sed in dui. Suspendisse tempor interdum nisi eu porta. Morbi dignissim nisl augue, ac accumsan erat efficitur et. Etiam vitae malesuada turpis. Sed cursus eu ligula sit amet euismod. Aliquam fermentum, mi in congue interdum", img: review}, 
    {name: "Reading academic articles", desc: "(SAMPLE TEXT) Lorem ipsum dolor sit amet, consectetur adipiscing elit. In eget quam sed nisl tincidunt blandit. Sed ut lacinia erat, eget commodo lorem. Sed interdum massa vitae libero mollis dignissim sed in dui. Suspendisse tempor interdum nisi eu porta. Morbi dignissim nisl augue, ac accumsan erat efficitur et. Etiam vitae malesuada turpis. Sed cursus eu ligula sit amet euismod. Aliquam fermentum, mi in congue interdum", img: read},
    {name: "Taking interviews", desc: "(SAMPLE TEXT) Lorem ipsum dolor sit amet, consectetur adipiscing elit. In eget quam sed nisl tincidunt blandit. Sed ut lacinia erat, eget commodo lorem. Sed interdum massa vitae libero mollis dignissim sed in dui. Suspendisse tempor interdum nisi eu porta. Morbi dignissim nisl augue, ac accumsan erat efficitur et. Etiam vitae malesuada turpis. Sed cursus eu ligula sit amet euismod. Aliquam fermentum, mi in congue interdum", img: interview},
    {name: "Teaching a class", desc: "(SAMPLE TEXT) Lorem ipsum dolor sit amet, consectetur adipiscing elit. In eget quam sed nisl tincidunt blandit. Sed ut lacinia erat, eget commodo lorem. Sed interdum massa vitae libero mollis dignissim sed in dui. Suspendisse tempor interdum nisi eu porta. Morbi dignissim nisl augue, ac accumsan erat efficitur et. Etiam vitae malesuada turpis. Sed cursus eu ligula sit amet euismod. Aliquam fermentum, mi in congue interdum", img: teach}
];


export default function TaskPage() {

    const [task, setTask] = useState({name: "", desc: ""});
    const [open, setOpen] = useState(false);

    useEffect(() => {
        [placeholder, sleep, review, read, interview, teach].forEach(src => {
            const img = new Image();
            img.src = src.fileName;
        });
    }, []);

    function handleRoll() {
        const interval = setInterval(() => {
            let randIndex = Math.floor(Math.random()*(taskList.length));
            let randColor = Math.floor(Math.random()*16777215).toString(16);
            setTask({...taskList[randIndex], desc: "", color: `#${randColor}`});
        }, 100);
        setTimeout(() => {
            clearInterval(interval);
            if(new Date().getHours < 5){
                setTask(taskList[0]);
            }else{
                let randIndex = Math.floor(Math.random()*(taskList.length-1) + 1);
                setTask(taskList[randIndex]);
            }
            setOpen(true);
        }, 4000);
    }

    return (
        <div>
            <div className="task-interactive">
                <div className="task-title">
                    It's {new Date().toLocaleTimeString().slice(0,-3)}! As a researcher at LSE, you could be...
                </div>
                <div className="task-btn">
                    <button onClick={handleRoll}>Roll!</button>
                </div>
                <div className="task-task" style={{"color": task.color}}>
                    <div className="task-name">{task.name || "Click the button above to roll a task!"}</div>
                    <div className="task-img"><img src={task.img || placeholder} /></div>
                    <div className="task-desc">{task.desc}</div>
                </div>  
            </div>
            {open && (
                <div className="task-content">
                    (SAMPLE TEXT) Researchers at LSE...
                    Lorem ipsum dolor sit amet, consectetur adipiscing elit. In eget quam sed nisl tincidunt blandit. Sed ut lacinia erat, eget commodo lorem. Sed interdum massa vitae libero mollis dignissim sed in dui. Suspendisse tempor interdum nisi eu porta. Morbi dignissim nisl augue, ac accumsan erat efficitur et. Etiam vitae malesuada turpis. Sed cursus eu ligula sit amet euismod. Aliquam fermentum, mi in congue interdum, massa elit porttitor mi, non pharetra neque leo ut nisi. Maecenas purus ipsum, porttitor quis gravida quis, scelerisque nec tellus. Aliquam commodo mauris a gravida maximus. Sed urna leo, consequat sed aliquet condimentum, ultricies at felis.
                </div>
            )}
            
        </div>
    )
}
